@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../../fonts/ptsans.woff2") format("woff2");
  font-display: swap;
}
body {
  font-family: "PT Sans", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  font-family: "PT Sans", sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-grow: 1;
  width: 100%;
  margin: 0;
  background-color: rgb(242, 242, 242);
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
  object-position: center;
  user-select: none;
}

fieldset {
  border: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
}

.wrapper {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  background-color: rgb(255, 255, 255);
  box-shadow: 0 0 20px rgba(0, 1, 1, 0.2);
}

.control-mark {
  position: absolute;
  left: 0;
  width: 46px;
  height: 46px;
  background-color: rgb(242, 242, 242);
  pointer-events: none;
}

.control:hover + .control-mark {
  background-color: rgb(229, 229, 229);
  transition-duration: 0.3s;
}

.control-mark:hover {
  background-color: rgb(229, 229, 229);
  transition-duration: 0.3s;
}

.control-mark:active {
  background-color: rgb(213, 213, 213);
}

.control-input[type=radio] + .control-mark {
  border-radius: 50%;
}

.control-input[type=radio]:checked + .control-mark::before {
  display: block;
  content: "";
  position: absolute;
  width: 16px;
  height: 16px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background-color: rgb(0, 0, 0);
  border-radius: 50%;
}

.control-input[type=radio]:checked:active + .control-mark::before {
  opacity: 0.3;
}

.control-input[type=checkbox]:checked + .control-mark {
  background-image: url("../img/tick.svg");
  background-repeat: no-repeat;
  background-position: 50% 50%;
  pointer-events: 0;
}

