.blue-form-button {
  background-color: rgb(129, 179, 210);
  color: rgb(255, 255, 255);
  text-transform: uppercase;
  border: none;
  display: block;
  font-family: "PT Sans", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  font-size: 21px;
}

.blue-form-button:hover {
  background-color: rgb(102, 158, 192);
  transition-duration: 0.3s;
}

.media-item {
  position: relative;
}

.media-item:hover {
  background-color: rgb(102, 158, 192);
  transition-duration: 0.3s;
}

.media-item:active {
  background-color: rgb(84, 150, 189);
}

.media-item::before {
  position: absolute;
  display: block;
  content: "";
  width: 24px;
  height: 14px;
  background-image: url("../img/vk.svg");
  background-repeat: no-repeat;
  background-position: 0 0;
  z-index: 1;
}

.media-item--vk::before {
  background-image: url("../img/vk.svg");
}

.media-item--tg::before {
  background-image: url("../img/telegram.svg");
  width: 21px;
  height: 18px;
}

.media-item--yt::before {
  background-image: url("../img/youtube.svg");
  width: 20px;
}

.media-item:active + .media-item::before {
  opacity: 0.1;
}

.modal-form {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 5;
}

.closed {
  display: none;
}

.opened-flex {
  display: flex;
  transform: translateY(0);
  transition-duration: 0.3s;
}

.opened-block {
  display: block;
}